import React, { Component } from 'react';

export default class Form extends Component{
  render() {
    return(
      <div className="form-wrapper">
        <button onClick={ this.props.closeForm } id="close-form"></button>
        <h2 className="m-bebas-20"> </h2>
        <p className="m-helvetica-12-bold">      </p>
        <form id="form">
          <input type="text" name="company" placeholder="  "  className="m-bebas-14" />
          <hr/>
          <input type="text" name="name" placeholder="  "  className="m-bebas-14" />
          <hr/>
          <input type="text" name="job" placeholder=""
          className="m-bebas-14" />
          <hr/>
          <input type="mail" name="mail" placeholder=". "
          className="m-bebas-14" />
          <hr/>
          <input type="phone" name="phone" placeholder=""
          className="m-bebas-14" />
          <hr/>
          <button type="submit" className="m-bebas-20"></button>
        </form>
      </div>
    )
  }
}
