import React from 'react'
import {Link} from 'react-router-dom'
import DialogWindow from 'components/dialogWindow.component'

import chocolate_with_sea_salt_poster from 'assets/images/chocolate_with_sea_salt_poster.png'
import status_love_poster from 'assets/images/status_love_poster.png'

const Projects = ({className}) => {
  return(
    <div className={className}>
      <Project
        className={`${className + '_project'}`}
        poster={chocolate_with_sea_salt_poster}
        title="   "
        genre=" "
        length="100 "
        status=" "
        forecast="1.5  "
        publisher=" "
        writer=" "
        description="   ,  
               
           :     
              
               
            .   
        "
        link="/projects/sea-salt-chocolate"
      />

      <Project
        className={`${className + '_project'}`}
        poster={status_love_poster}
        title="  "
        genre=" "
        length="90 "
        status=" "
        forecast="1.5  "
        publisher=" "
        writer=" "
        description="     
        ,      .  
             , ,  
        ,  ,     
         ,      , 
        ,  , ,  
         .
        "
        link="/projects/status-love"
      />
    </div>
  );
}

const Project =
({
  className,
  poster,
  title,
  genre,
  length,
  status,
  forecast,
  publisher,
  writer,
  description,
  link
}) => (
  <div className={className}>
    <img src={poster} alt=""/>
    <h3>{title}</h3>
    <ul>
      <li><span>:&nbsp;</span>{genre}</li>
      <li><span>:&nbsp;</span>{length}</li>
      <li><span>:&nbsp;</span>{status}</li>
      <li><span> :&nbsp;</span>{forecast}</li>
      <li><span>:&nbsp;</span>{publisher}</li>
      <li>
        <span>:&nbsp;</span>
        <DialogWindow text={writer}>
          <h4> </h4>
          <p>  ,   </p>
        </DialogWindow>
      </li>
    </ul>
    <p>{description}</p>
    <Link to={link}>  </Link>
  </div>
)

export default Projects;
